@charset 'UTF-8';

@include media-breakpoint-down(md) {
  .accordion-body .vd-highlight {
    ol, ul {
      padding-left: 20px;
    }
  }
}

.accordion-button {
  padding-right: 7em;
  cursor: pointer;

  &:after {
    position: absolute;
    right: 1.3em;
    top: 1.2rem;
  }

  &:before {
    Content: 'Ouvrir';
    float: right;
    position: absolute;
    right: 3em;
  }

  &:not(.collapsed):before {
    content: 'Fermer';
  }
}

.accordion-button-subheader {
  &:after {
    top: 2.1rem;
  }

  &:before {
    top: 1.9rem;
  }
}

.accordion-standalone .accordion-item:not(:first-of-type) {
  border: var(--bs-accordion-border-width) solid
  var(--bs-accordion-border-color);
}
